const getSuggestionValue = suggestion => suggestion;

const renderSuggestion = suggestion => (
  <div> {suggestion.replace(/_/gi, " ")} </div>
);

export default class App extends React.Component {
  state = {
    persons: [],
    continent: "America",
    selectedContinent: "America",
    city: "",
    citySearch: "",
    data,
    suggestions: data["America"],
    selection: [],
    lat: [],
    lng: [],
    center: {
      lat: [],
      lng: []
    },
    categories: [],
    time: "",
    timeStamp: ""
  };

  getSuggestions = value => {
    const inputValue = value.trim().toLowerCase();
    const inputLength = inputValue.length;
    return inputLength === 0
      ? this.state.data[this.state.selectedContinent]
      : this.state.data[this.state.selectedContinent].filter(
          city => city.toLowerCase().slice(0, inputLength) === inputValue
        );
  };

  // Autosuggest will call this function every time you need to update suggestions.
  // You already implemented this logic above, so just use it.
  onSuggestionsFetchRequested = ({ value }) => {
    this.setState({
      suggestions: this.getSuggestions(value)
    });
  };

  // Autosuggest will call this function every time you need to clear suggestions.
  onSuggestionsClearRequested = () => {
    this.setState({
      suggestions: []
    });
  };

  constructor(props) {
    super(props);
    this.searchResultsRef = React.createRef();
    this.searchPage = React.createRef();
  }

  timeStamp = (continent, city) => {
    let x = new Date().toLocaleString("en-US", {
      timeZone: continent + "/" + city,
      hour12: false,
      weekday: "long",
      month: "long",
      day: "numeric",
      hour: "2-digit",
      minute: "2-digit"
    });
    return x;
  };

  handleChange = event => {
    this.setState({
      citySearch: event.target.value
    });
  };

  onChange = (event, { newValue }) => {
    this.setState({
      citySearch: newValue
    });
  };

  handleChangeSelect = event => {
    const value = event.target.value;
    this.setState(state => ({
      selectedContinent: value,
      suggestions: state.data[value]
    }));
  };

  handleSubmit = e => {
    e.preventDefault();

    this.setState(
      state => ({
        city: state.citySearch,
        continent: state.selectedContinent
      }),
      () => {
        this.getSelectionfromApi(this.state.city, this.state.time);
      }
    );
  };

  getTime = () => {
    let x = this.timeStamp(this.state.continent, this.state.city);
    let y = x.match(/(?:[01]\d|2[0123]):(?:[012345]\d)/);
    let yy = y[0];
    let yyy = Number(yy.replace(":", ""));
    let time = "";
    if (yyy > 0o600 && yyy <= 1200) {
      time = "Breakfast";
    } else if (yyy > 1200 && yyy < 1700) {
      time = "Lunch";
    } else {
      time = "Dinner";
    }
    this.setState({ time: time, timeStamp: x });
  };

  getSelectionfromApi = (city, meal) => {
    axios
      .get(
        `${"https://cors-anywhere.herokuapp.com/"}https://api.yelp.com/v3/businesses/search?location=` +
          city,
        {
          headers: {
            Authorization: `Bearer 6HpwezQbGeEEFr-zNNAbretclit-SFBBqV92zLarZ5hOAEmiL-In5gB2ty70Q7YQTRDROV7Oq3zFRxvyX8zeEWiQsj6DGQj2mtblF3ZHyHQk8g-Am3hdv3qG64Y_XHYx`
          },
          params: {
            categories: meal
          }
        }
      )
      .then(res => {
        let businesses = res.data.businesses;
        this.setState({ selection: businesses }, () => {
          this.getTime();
          this.getCoordinates(businesses);
        });
        this.searchResultsRef.current.scrollIntoView({
          block: "nearest",
          behavior: "smooth"
        });
      })
      .catch(err => {
        console.log("err", err);
      });
  };

  getCoordinates = businesses => {
    let x = businesses.map(item => {
      let lat = item.coordinates.latitude;
      return lat;
    });
    let y = businesses.map(item => {
      let lng = item.coordinates.longitude;
      return lng;
    });
    this.setState(state => ({ lat: x, lng: y }));
  };

  scroll = () => {
    this.searchPage.current.scrollIntoView({
      block: "nearest",
      behavior: "smooth"
    });
  };

  render() {
    const { citySearch, suggestions } = this.state;
    // Autosuggest will pass through all these props to the input.
    const inputProps = {
      placeholder: "Select an area",
      value: citySearch,
      onChange: this.onChange
    };

    return (
      <>
        <div id="videocontainer">
          <video
            muted
            autoPlay
            id="video"
            loop
            alt="coffee machine brewing coffee onto white cup"
          >
            <source src={video} />{" "}
          </video>{" "}
        </div>{" "}
        <div className="hero">
          <h1> MealTime </h1>{" "}
        </div>{" "}
        <About scroll={this.scroll} />{" "}
        <div className="hero" id="selectionPage" ref={this.searchPage}>
          <h1> MealTime </h1>{" "}
          <div className="selection">
            <form action="#">
              <select
                onChange={this.handleChangeSelect}
                value={this.state.selectedContinent}
                className="react-autosuggest__container"
              >
                {Object.keys(this.state.data).map(continent => (
                  <option
                    value={continent}
                    className="react-autosuggest__input "
                  >
                    {continent}{" "}
                  </option>
                ))}{" "}
              </select>{" "}
              <Autosuggest
                suggestions={suggestions}
                shouldRenderSuggestions={() => true}
                onSuggestionsFetchRequested={this.onSuggestionsFetchRequested}
                onSuggestionsClearRequested={this.onSuggestionsClearRequested}
                getSuggestionValue={getSuggestionValue}
                renderSuggestion={renderSuggestion}
                inputProps={inputProps}
              />{" "}
              <input
                type="submit"
                value="submit"
                onClick={this.handleSubmit}
                disabled={
                  !this.state.data[this.state.selectedContinent].includes(
                    this.state.citySearch
                  )
                }
              />{" "}
            </form>{" "}
          </div>{" "}
        </div>{" "}
        <div ref={this.searchResultsRef}>
          {" "}
          {this.state.city.length > 0 && (
            <div className="searchPage">
              <ShowTime
                time={this.state.time}
                city={this.state.city}
                timeStamp={this.state.timeStamp}
              />{" "}
              <Yelp
                city={this.state.city}
                getSelectionfromApi={this.getSelectionfromApi}
                selection={this.state.selection}
                lat={this.state.lat}
                lats={this.state.lat}
                lng={this.state.lat}
                center={this.state.center}
              />{" "}
            </div>
          )}{" "}
        </div>{" "}
      </>
    );
  }
}
