const time = [];

const filtereddata = all
  .filter(data => data.includes("/"))
  .filter(timezone => {
    try {
      new Date().toLocaleString("en-US", {
        timeZone: timezone,
        hour12: false,
        weekday: "long",
        month: "long",
        day: "numeric",
        hour: "2-digit",
        minute: "2-digit"
      });
    } catch (error) {
      //console.log("timezone: ", timezone);
      return false;
    }
    return true;
  });

const reducedData = filtereddata.reduce((acc, curr) => {
  const currContinent = curr.substring(0, curr.indexOf("/"));
  const currCity = curr.substring(curr.indexOf("/") + 1);
  if (!acc[currContinent]) {
    acc[currContinent] = [];
  }
  if (!acc[currContinent].includes(currCity)) {
    acc[currContinent].push(currCity);
  }
  return acc;
}, {});

export { all, data, reducedData };
